import { clear } from '@testing-library/user-event/dist/clear';
import axios from 'axios';
import React, {useState, useEffect, useContext} from 'react';
import { useHistory,useParams } from 'react-router-dom/cjs/react-router-dom.min';
import styled  from 'styled-components';
import './Detail.scss';
import {재고context} from './App.js'; 
import {Nav, Tab} from 'react-bootstrap'
import {CSSTransition} from "react-transition-group";
import { connect } from 'react-redux';

let 박스 = styled.div`
  padding-top : 30px;
;`
let 제목= styled.div`
  font-size : 25px;
  color : ${ props => props.색상}
;`


// class Detail2 extends React.Component {
  
//   componentDidMount(){//Detail2 컴포넌트가 Mount 되었을 때 실행할 코드~

//   }
//   componentWillUnmount(){//Detail2 컴포넌트가 Unmount(장착이 해제되기) 직전에 실행할 코드~

//   }
// }




function Detail(props){

    let [alert, alert변경] = useState(true);
    let [inputData,inputData변경] = useState('');
    let [누른탭, 누른탭변경] = useState(0);
    let [스위치, 스위치변경] = useState(false);

    let 재고 = useContext(재고context);
     

    useEffect(()=>{
      axios.get()

      let 타이머 = setTimeout(()=>{  alert변경(false)  }, 2000) ;
      console.log('안녕');
      return ()=>{ clear.Timeout(타이머)}

      //return function 어쩌구(){  } 
    },[]);

    let { id } = useParams();
    let history = useHistory();
    let 찾은상품 = props.shoes.find(function(상품){
      return 상품.id == id
    })
// let 찾은상품 = props.shoes(x = x.id == id);
    
    return(
      <div className="container">
        <박스>
        <제목 className="red">상품 상세페이지</제목>
        </박스> 

        
        

        {
          alert === true
          ? (<div className="my-alert2">
            <p>재고가 얼마 남지 않았습니다</p>
            </div>)
          : null
        }

          <div className="row">
            <div className="col-md-6">
              <img src="https://codingapple1.github.io/shop/shoes1.jpg" width="100%" />
            </div>
            <div className="col-md-6 mt-4">
              <h4 className="pt-5">{찾은상품.title}</h4>
              <p>{찾은상품.content}</p>
              <p>{찾은상품.price}원</p>
              
              <Info 재고={props.재고}></Info>
              

              <button className="btn btn-danger" onClick={()=>{ 
                props.재고변경([9,11,12]); 
                props.dispatch({type: '항목추가', 데이터 : {id:찾은상품.id, name: 찾은상품.title, quan:1}});
                history.push('/cart');}}>주문하기</button>
              &nbsp;
              <button className="btn btn-danger" onClick={()=>{history.push('/')}}>뒤로가기</button>
            </div>
          </div>

          <Nav className="mt-5" variant="tabs" defaultActiveKey="link-0">
            <Nav.Item>
              <Nav.Link eventKey="link-0" onClick={()=>{스위치변경(false); 누른탭변경(0)}}>Active</Nav.Link>
            </Nav.Item>
            <Nav.Item>
              <Nav.Link eventKey="link-1" onClick={()=>{스위치변경(false); 누른탭변경(1)}}>Option 2</Nav.Link>
            </Nav.Item>
            <Nav.Item>
              <Nav.Link eventKey="disabled" disabled></Nav.Link>
            </Nav.Item>
          </Nav>
          <CSSTransition in={스위치} classNames="wow" timeout={500}>
          <TabContent 누른탭={누른탭} 스위치변경={스위치변경}/>
          </CSSTransition>

        </div> 
    )
  }

  function TabContent(props){

    useEffect(()=>{
      props.스위치변경(true);
    });

    if(props.누른탭 === 0) {
      return <div>0번째</div>
    }else if (props.누른탭 === 1){
      return <div>1번째</div>
    }else if (props.누른탭 === 2){
      return <div>2번째</div>
    }

  }


  function Info(props) {
    return (
      <p>재고 : {props.재고[0]}</p>
    )

  }

  function state를props화(state){
    return {
        state : state.reducer,  //store에 있는 데이터를 state라는 이름의 props로 바꿔주셈
        alert열렸니 : state.reducer2
    }

}

    export default connect(state를props화)(Detail)
    //export default Detail